.service-works {
  margin: 4rem 0;
}

.service-works_container .title-container {
  text-align: center;
  margin: 0 auto 3rem;
  max-width: 868px;
}

.service-works_container .title-container h2 {
  color: #004936;
  font-weight: 700;
  font-size: 24px;
  line-height: 32px;
  letter-spacing: 1px;
  margin-bottom: 2.688rem;
}

.service-works_container .title-container h3 {
  font-size: 18px;
  line-height: 32px;
  color: #4E4B66;
  letter-spacing: 0.75px;
  font-weight: 400;
}

.service-works-card {
  gap: 2rem;
}

.service-works-card--item {
  padding: 2rem;
  box-sizing: border-box;
  background: #FCFCFC;
  border: 1px solid rgba(0, 0, 0, 0.05);
  box-shadow: 0px 0px 16px rgba(0, 0, 0, 0.04);
  border-radius: 32px;
}

.service-works-card--item .image-container {
  overflow: hidden;
  border-radius: 2rem;
}

.service-works-card--item .image-container img {
  transition: transform 0.5s ease;
}

.service-works-card--item .service-workds-card_content {
  margin-top: 1.5rem;
}

.service-works-card--item .service-workds-card_content h3 {
  color: #14142B;
  font-size: 20px;
  line-height: 32px;
  font-weight: 700;
  letter-spacing: 1px;
}

.service-works-card--item .service-workds-card_content p {
  font-weight: 400;
  font-size: 18px;
  line-height: 32px;
  letter-spacing: 0.75px;
  color: #4E4B66;
}

@media (min-width: 992px) {
  .service-works_container .title-container h2 {
    font-size: 48px;
    line-height: 56px;
  }
  .service-workds-card_content {
    margin-top: 1.5rem;
  }
  .service-workds-card_content h3 {
    font-size: 1.5rem;
    line-height: 32px;
  }
  .service-works-card--item:hover .image-container img {
    transform: scale(1.1);
  }
}